import React, { useState, useEffect } from 'react'
import './App.css'
import deliveryService from './services/deliveries'

const Ticket = ({ address, total, payment, handleClick, handleAddress, handleTotal, handlePayment, handleTip }) => {
  return (
    <div className="center">
      <div>
        <label>Address: </label><input value={address} onChange={handleAddress} />
      </div>
      <div>
      <label>Total: </label><input placeholder="$0.00" value={total} onChange={handleTotal} />
      </div>
      <div>
      <label>Payment/Tip: </label><input placeholder="$0.00" value={payment} onChange={handlePayment} />
      </div>
      <button className="add" type="submit" onClick={handleClick}><b>Add Delivery</b></button>
      <button className="add" type="submit" onClick={handleTip}><b>Add Tip</b></button>
    </div>
  )
}

const Display = ({ deliveries, handleDelete }) => {
  return (
    <ol>
      {deliveries.map(delivery => 
        <li className="list" key={delivery.id}>
          <div className="top"><b>Address: {delivery.address}</b></div> <div className="center">Total: ${delivery.total}</div> <div className="center">Payment: ${delivery.payment}</div><button className="add"><b>Update</b></button><button className="delete" value={delivery.id} onClick={handleDelete} ><b>Delete</b></button>
        </li>)}
    </ol>
  )
}

const Pay = ({ deliveries, tips }) => {
  let pay = 0 + tips
  deliveries.map(delivery => pay += (parseFloat(delivery.payment) - parseFloat(delivery.total)))
  return (
    <div className="center">
      <h2>${pay.toFixed(2)}</h2>
    </div>
  )
}

const Method = ({ method, handleMethod, address, total, payment, handleClick, handleAddress, handleTotal, handlePayment, handleTip, deliveries, tips, handleDelete }) => {
  if (method) {
    return (
      <div className="div-method">
        <button onClick={handleMethod} value="Ticket" className="method-selected">Ticket</button>
        <button onClick={handleMethod} value="Previous Deliveries" className="method">Previous Deliveries</button>
        <Ticket address={address} total={total} payment={payment} handleClick={handleClick} handleAddress={handleAddress} handleTotal={handleTotal} handlePayment={handlePayment} handleTip={handleTip} />
        <Pay deliveries={deliveries} tips={tips} />
      </div>
    )
  }
  return (
    <div className="div-method">
      <button onClick={handleMethod} value="Ticket" className="method">Ticket</button>
      <button onClick={handleMethod} value="Previous Deliveries" className="method-selected">Previous Deliveries</button>
      <Display deliveries={deliveries} handleDelete={handleDelete} />
    </div>
  )
}

const App = () => {
  const [ deliveries, setDeliveries ] = useState([])
  const [ address, setAddress ] = useState('')
  const [ total, setTotal ] = useState('')
  const [ payment, setPayment ] = useState('')
  const [ method, setMethod ] = useState(true)
  const [ tips, setTips ] = useState(0)

  useEffect(() => {
    deliveryService
    .getAll()
    .then(initialDeliveries => {
      setDeliveries(initialDeliveries)
    })
  }, [])

  const handleAddress = (event) => {
    setAddress(event.target.value)
  }

  const handleTotal = (event) => {
    setTotal(event.target.value)
  }

  const handlePayment = (event) => {
    setPayment(event.target.value)
  }

  const handleMethod = (event) => {
    if (method && event.target.value === "Ticket") {
      return
    }
    else if (method === false && event.target.value === "Previous Deliveries") {
      return
    }
    setMethod(!method)
  }

  const handleTip = (event) => {
    if (!Number.isInteger(parseInt(payment))) {
      window.alert('Enter a valid input for tip')
      setPayment('')
      return
    }
    const totalTips = tips + parseFloat(payment)
    setTips(totalTips)
    setPayment('')
  }

  const handleDelete = (event) => {
    console.log(event.currentTarget.value)
    deliveryService
      .remove(event.currentTarget.value)
      .then(() => {
        setDeliveries(deliveries.filter(delivery => delivery.id != event.currentTarget.value))
      })
  }

  const handleClick = (event) => {
    if (!Number.isInteger(parseInt(total)) || !Number.isInteger(parseInt(payment))) {
      window.alert('Enter a valid input for total and payment')
      setTotal('')
      setPayment('')
      return
    }

    const newDelivery = {
      address: address,
      total: total,
      payment: payment
    }

    deliveryService
      .create(newDelivery)
      .then(newDelivery => {
        setDeliveries(deliveries.concat(newDelivery))
        setAddress('')
        setTotal('')
        setPayment('')
      })

  }

  return (
    <div>
      <div className="banner">
        <h1>Delivery Tracker</h1>
      </div>
      <Method handleMethod={handleMethod} method={method} address={address} total={total} payment={payment} handleClick={handleClick} handleAddress={handleAddress} handleTotal={handleTotal} handlePayment={handlePayment} handleTip={handleTip} deliveries={deliveries} tips={tips} handleDelete={handleDelete} />
    </div>
  )
}

export default App